export default function reducer(state, action) {
  switch(action.type) {
    /*  */
    case 'changeTheme':
      return {
        btnCaption : state.btnCaption === "Night"? "Light": "Night",
        theme: !state.theme,
      }

    /*     */
    case 'request': //    ""
      return {...state, ...action.payload, ...{visibleForm:!state.visibleForm}, ...{numberTodo:action.numberTodo}}

    /*    (,  ,  )*/
    case 'addTask': {//    "Add Task"
      return [
        ...state,
        {
          ...action.payload
        },
      ]
    }/**  */
    case 'isComplete':{
      return [
        ...state
      ]
    }/**  */
    case 'isDelete':{
      const newState = state.filter((task) => !task.isDelete);
      return [...newState]
    }
    case 'changeTask':{
      state.forEach( (task) => {
        if(task.id == action.id){
          task.text = "text change"
        }
      })
      return [...state];
    }
    case 'editTask':{
      return !action.editTask;
    }
    default:
      throw new Error("  reducer")
  }
}
